<template>
  <div class="row">
    <div v-if="dados && dados.length">
      <v-card-title>
        <v-text-field
          v-model="search"
          label="Digite o CPF do Funcionário"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
      </v-card-title>
      <v-data-table
        :headers="header"
        :items="dados"
        :search="search"
        :rows-per-page-items="[20]"
      >
        <template slot="items" slot-scope="props">
          <td  v-if="props.item.cpf">{{ props.item.cpf }}</td>
          <td v-if="props.item.nome">
            <router-link to="a">
              {{ props.item.nome }}
            </router-link>
          </td>
          <td  v-if="props.item.completo">{{ props.item.completo }}</td>
          <td  v-if="props.item.integrado">{{ props.item.integrado }}</td>
          <td class="left"><router-link to="id"><i class="material-icons">edit</i></router-link></td>
        </template>
        <v-alert slot="no-results" :value="true" color="error" >
          Sua pesquisa por "{{ search }}" não encontrou nenhum registro.
        </v-alert>
      </v-data-table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TableGird',
  props: ['header', 'table', 'nome'],
  data () {
    return {
      search: '',
      dados: null,
      delSucesso: false,
      delErro: false,
      delMsgErro: null
    }
  },
  created () {
    this.listar()
  },
  methods: {
    listar () {
      this.dados = [
        {
