          cpf: '123.123.123-12',
          completo: 'n',
          integrado: 's'
        },
        {
          nome: 'Allan',
          cpf: '455.456.456-48',
          completo: 'n',
          integrado: 's'
        }
      ]
    }
    // listar () {
    //   this.$http.get(this.$dbUrl + 'servicos/' + this.table, {
    //     'headers': {
    //       'authorization': 'Bearer ' + this.$store.getters.getToken
    //     }
    //   }).then(response => {
    //     if (response.data.status) {
    //       this.dados = response.data[this.table]
    //     }
    //   }).catch(e => {
    //     this.delMsgErro = e.response.data.message
    //     this.delSucesso = false
    //     this.delErro = true
    //   })
    // },
    // deletar (id) {
    //   this.$http.delete(this.$dbUrl + 'servicos/' + this.table + '/' + id, {
    //     'headers': {
    //       'authorization': 'Bearer ' + this.$store.getters.getToken
    //     }
    //   }).then(response => {
    //     if (response.data.status === true) {
    //       this.delSucesso = true
    //       this.delErro = false
    //       this.listar()
    //     }
    //   }).catch(e => {
    //     this.delMsgErro = e.response.data.message
    //     this.delSucesso = false
    //     this.delErro = true
    //   })
    // }
  }
}
</script>
<style scoped>
.error {
  color: red !important;
  background-color: white !important;
}
a {
  cursor: pointer;
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: underline;
}
.datatable__progress {
  display: none !important;
}
</style>



