<template>
  <SiteTemplate>
    <v-layout>
      <v-flex xs12 sm12>
        <TableGrid nome="Funcionario" table='empresas' :header="header"/>
      </v-flex>
    </v-layout>
  </SiteTemplate>
</template>
<script>
import SiteTemplate from '@/templates/SiteTemplate'
import TableGrid from '@/components/TableGrid'
export default {
  name: 'Home',
  data () {
    return {
      header: [
        { text: 'CPF', value: 'cpf', sortable: false },
        { text: 'Nome do Funcionario', value: 'nome' },
        { text: 'Completo', value: 'completo', sortable: false },
        { text: 'Integrado', value: 'integrado', sortable: false },
        { text: 'Ações', value: '', sortable: false }
      ]
    }
  },
  components: {
    SiteTemplate,
    TableGrid
  }
}
</script>



