<template>
  <div>
    <div class="divider"></div>
    <v-layout class="title">
      <v-flex xs12>
        <span :id="title"> {{ title }} </span>
      </v-flex>
    </v-layout>
    <div class="divider"></div>
      <slot></slot>
  </div>
</template>

<script>
export default {
  props: ['title', 'titleSeparator']
}
</script>

<style scoped>
.title {
  font-size: 2em;
  font-family: serif !important;
  /* background-color: #eee; */
}
.divider {
  padding: 1.3%;
}
.horizontal-divider {
  border-top: 0.5px dashed black;
}
</style>



