<template>
  <v-expansion-panel focusable>
    <v-expansion-panel-content
      v-for="(item, i) in options"
      :key="i"
      >
      <div slot="header">{{ item.title }}</div>
      <v-card v-for="(subitem, i) in item.suboptions" :key="i">
        <router-link :to="subitem.link">
          <v-card-text class="active">{{ subitem.nome}}</v-card-text>
        </router-link>
      </v-card>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>
<script>
export default {
  data () {
    return {
      options: [
        {
          title: 'Dados Gerais',
          suboptions: [
            {
              link: '#',
              nome: 'Dados Gerais'
            },
            {
              link: '#',
              nome: 'CPF'
            }
          ]
        }
      ]
    }
  }
}
</script>



