<template>
  <LoginTemplate>
      <span slot="title">
        Esqueceu sua senha? Mandaremos outra para você!
      </span>

      <v-flex slot="form">
        <v-text-field v-model="email"
          label="E-mail"
          :rules="[rules.required, rules.email]"
          required
        ></v-text-field>
      </v-flex>

      <v-flex left slot="buttons">
        <v-btn
        class="text-white"
        color="blue"
        large
        @click="submit()"
        > Enviar Nova Senha </v-btn>
        <router-link to="login" class="no-text-decoration">
          <v-btn large left>
              Cancelar
          </v-btn>
        </router-link>
      </v-flex>
  </LoginTemplate>
</template>

<script>
import LoginTemplate from '@/templates/LoginTemplate'
export default {
  data: () => ({

    rules: {
      required: value => !!value || 'Required.',
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || 'Invalid e-mail.'
      }
    }
  }),
  components: {
    LoginTemplate
  },
  methods: {
    submit () {
      if (this.$refs.form.validate()) {

      }
    }
  }
}
</script>

<style scoped>
.label-pink {
  color: #e91e63!important;
}
</style>

<style>
.forgot {
  text-align: right;
}
</style>



