<template>
  <LoginTemplate>
    <span slot="title">
      Bem-vindo
    </span>
    <v-flex slot="form">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field v-model="email"
          prepend-icon="account_circle"
          label="E-mail"
          :rules="[rules.required, rules.email]"
          required
        ></v-text-field>

        <v-text-field v-model="password"
          prepend-icon="lock"
          :append-icon="showPass ? 'visibility_off' : 'visibility'"
          :type="showPass ? 'text' : 'password'"
          :rules="[rules.required, rules.min]"
          label="Senha"
          required
          @click:append="showPass = !showPass"
        ></v-text-field>
      </v-form>
    </v-flex>

    <v-layout wrap slot="buttons">
      <v-flex x12 s6 pl-4>
          <v-btn
          color="blue"
          class="text-white"
          @click="submit()"
          large
          >Entrar</v-btn>
      </v-flex>

      <v-flex x12 s6 text-xs-right>
        <router-link
        to="forgotpassword"
        class="no-text-decoration">
          <v-btn
          large>
            Esqueceu sua senha?
          </v-btn>
        </router-link>
      </v-flex>
    </v-layout>
  </LoginTemplate>
</template>

<script>
import LoginTemplate from '@/templates/LoginTemplate'
export default {
  data () {
    return {
      email: '',
      password: '',
      showPass: false,
      rules: {
        required: value => !!value || 'Required.',
        min: value => value.length >= 5 || 'Min 5 character',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        }
      },
      valid: false
    }
  },
  components: {
    LoginTemplate
  },
  methods: {
    submit () {
      if (this.$refs.form.validate()) {
        this.$router.push('/')
      }
    }
  }
}
</script>



